@import "slick-carousel/slick/slick.css";
@import "slick-carousel/slick/slick-theme.css";

@import url('https://fonts.googleapis.com/css2?family=Archivo:wght@100;200;300;400;500;600;700;800;900&display=swap');



@font-face {
  font-family: myFont;
  src: url(OTF/ClashGrotesk-Bold.otf);
}
@font-face {
  font-family: myFont2;
  src: url(OTF/ClashGrotesk-Medium.otf);
}
*{
  margin: 0;
  padding: 0;
  font-family: 'Archivo';
}  

html{
  scroll-behavior: smooth;
  overflow-x: hidden;
}

@font-face {
  font-family:"clash-g-bold";
  src: url("../fonts/ClashGrotesk_Complete/Fonts/WEB/fonts/ClashGrotesk-Bold.ttf");
}

.mainTitle{
  font-family:"clash-g-bold";
  color: #091E42;
  font-size: 2.5em;
  font-weight: 700;
}

.mainBtn{
    background: #0047B3;
    border: 2px solid #0047B3;
    border-radius: 8px;
    text-decoration: none !important;
    transition: all ease-in-out 200ms;
    padding: 0.5em 2em !important;
    display: inline-block;
    color: #E6F0FF !important;
}

.mainBtn:hover{
  transform: scale(0.9);
}

/* login page */
.loginPage{
  padding: 5em 0;
}

.loginPage form{
  background: #F6F6F6;
border: 1px solid #006BA7;
border-radius: 10px;
padding: 10em 3em 15em 3em;
}

.loginPage form .mainBtn{
  margin-top: 7em;
  padding: 0.5em 7em;
}

.loginPage form label{
  font-family: 'Archivo', sans-serif;
  color: #091E42;
  font-weight: 600;
  font-size: 1.2em;
  margin-bottom: 0.5em;
}

.loginPage form input:not(input[type="checkbox"]){
  display: block;
  width: 100%;
  background: #FDFDFD;
border: 1px solid #091E42;
border-radius: 12px;
padding: 1.5em 1em;
padding-right: 5em;
color: #7A8699;
font-size: 1em;
margin-bottom: 3em;
}

.loginPage form .passwordBox{
  position: relative;
}

.loginPage form .passwordBox img{
  position: absolute;
  right: 1em;
  top: 50%;
  transform: translateY(-50%);
  cursor: pointer;
}

.loginPage form .forgot{
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.loginPage form .forgot a{
  text-decoration: none;
  color: #0047B3;
  font-weight: 400;
  font-family: 'Archivo', sans-serif;
  transition: all ease-in-out 200ms;
}

.loginPage form .forgot a:hover{
  opacity: 0.7;
}

/* sidebar  */
.sideBar{
  width: 299px;
  box-shadow: 0px 8px 20px rgba(0, 0, 0, 0.25);
  background-color: #ffff;
  min-height: 100vh;
}

.sideBar .mainTitle{
  text-align: center;
  padding: 0.5em 0;
}

.sideBar .sideMenu{
  padding: 2em 1em;
  padding-right: 0;
}

.sideBar .sideMenu a{
  display: block;
padding: 1.2em 0.6em;
text-decoration: none;
color: #091E42;
font-size: 1.2em;
font-weight: 600;
}

.sideBar .sideMenu a:hover{
   background-color: transparent !important;
  color: #003E9C !important;
}

.sideBar .sideMenu a img{
  margin-right: 0.7em;
}

.sideBar .sideMenu .card a{
  font-weight: 400;
  transition: all ease-in-out 200ms;
}

.sideBar .sideMenu .card a:hover{
  background-color: #BDE4B5;
}

.sideBar .sideMenu .card{
  border: none !important;
  box-shadow: none !important;
  padding: 0;
  padding-left: 2em;
}

.sideCollapes{
  position: relative;
}

.sideCollapes .arrowdown{
  position: absolute;
  right: -7px;
  top: 50%;
  transform: translateY(-50%);
transition: all ease-in-out 200ms;
}

.sideBar a[aria-expanded="true"] .arrowdown{
  transform: translateY(-50%) rotate(180deg);
}

.sideBar a[aria-expanded="true"] {
border-right: 5px solid #003E9C;
}

/* nav bar */
.navBarSection .navBar{
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}

.navBarSection .navbar-nav{
  width: 100%;
}

.navBarSection .navBar .username{
  font-family:"clash-g-bold";
  color: #091E42;
  font-size: 1.4em;
  line-height: 0.5em;
  margin-top: 1em;
}
.navBarSection .navBar .userScope{
  color: #091E42;
  font-size: 1em;
  font-weight: 400;
}
.navBarSection .navBar .userInfo{
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1em;
}
.navBarSection .navBar .searchBox{
  position: relative;
  width: 30em;
}
.navBarSection .navBar .searchBox input{
  width: 100%;
  background: #EBEDF0;
border-radius: 8px;
padding: 0.5em 1em;
padding-left: 3em;
outline: none;
border: none;
}
.navBarSection .navBar .searchBox img{
  position: absolute;
  left: 0.5em;
  top: 50%;
  transform: translateY(-50%);
}
.navbar{
  background: #F6F6F6 !important;
border-bottom: 1px solid #B0B0B0;
}
.mainContent{
  width: calc(100% - 299px);
  background: #F6F6F6
}
.bodyWrapper{
  display: flex;
}
/* cards */
.progressCard{
  border: 0.846505px solid #A6AEBB;
  background-color: #ffffff;
border-radius: 13.5441px;
padding: 0.5em;
transition: 300ms all ease-in-out;
}
.progressCard:hover {
  box-shadow: 0px 9px 18px #909090;
}
.progressCard .cardHeader{
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 3em;

}
.progressCard .cardHeader .date{
  color: #091E42;
  font-size: 0.9em;
  font-weight: 400;
  margin: 0;

}
.progressCard .cardHeading{
  color: #091E42;
  font-size: 1.2em;
  font-family:"clash-g-bold";
}
.progressCard h6{
  color: #091E42;
  font-size: 0.9em;
  font-weight: 400;
}
.progressCard .userimg{
  display: flex;
  align-items: center;
  gap: 0.5em;
}
.progressCard .cardFooter{
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.progressCard .cardFooter .daysLable{
  color: #091E42;
  font-size: 9.31155px;
  font-weight: 400;
  background: #E6F0FF;
  border-radius: 6.77204px;
  padding: 0.5em 1em;
}

.progressCard .dropdown .btn{
  border: none;
  box-shadow: none;
}

.progressCard .progress{
  margin-bottom: 3em;
  background: #BDE4B5;
}

.progressCard .progress-bar{
  background: #005696;
  border-radius: 42.3252px;
}
.progressCard .progressCount{
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.progressCard .progressCount h6{
  color: #091E42;
  font-size: 0.9em;
  font-weight: 400;
}

.overview{
  background-color: white;
}

.overviewCard{
  background: #F6F6F6;
  border-radius: 8px;
  padding: 0.5em 1em;
  margin: 1em 0;
}

.mainHeading{
  color: #091E42;
  font-size: 1.1em;
  font-weight: 600;
  margin: 0.5em 0;
}

/* table component */
.commanTable{
  /* overflow-x: auto; */
}

.commanTable table{
  width: 100%;
  background-color: #ffff;
}

.commanTable table th{
  background: #7AC769;
  padding: 0.5em 0.5em;
  font-weight: 600;
  color: #091E42;
  text-transform: capitalize;
}

.commanTable table th:first-child{
  border-top-left-radius: 8px;
}

.commanTable table th:last-child{
  border-top-right-radius: 8px;
}

.commanTable table td{
  border-bottom: 1px solid #F6F6F6;
  padding: 0.5em 0.5em;
  font-size: 1em;
  font-weight: 400;
  color: #091E42;
  /* text-transform: capitalize; */
}

.commonHeader{
  display: flex;
  align-items: center;
  justify-content: space-between;
 margin: 1em 0;
}

.commonBtn{
  color: #091E42;
  font-size: 0.7em;
  background: #E6F0FF;
  border-radius: 8px;
  padding: 0.5em 1em;
  text-decoration: none;
  font-weight: 400;
}

.commonBtn span{
  font-size: 1.5em;
  margin-right: 5px;
  font-weight: 600;
}

.testChart{
  width: 100%;
}

/* side bar right */
.rightSideBar{
  box-shadow: 0px 8px 20px rgba(0, 0, 0, 0.25);
  margin-top: -1em;
  margin-right: -1em;
  padding: 1em;
}

.rightSideBar .dropdown .btn{
  border: none;
  box-shadow: none;
  color: #1A1F36;
  font-weight: 400;
 
}
.rightSideBar .dropdown .btn span{
  color: #7A8699;
  font-size: 13px;
  font-weight: 400;
  margin-right: 1em;
}
 .smText{
  font-size: 0.8em;
  color: #091E42;
  font-weight: 400;
  cursor: pointer;
}
/* notification comp */
.notification {
  background: #E6F0FF;
  padding: 1em;

}
.notification .request{
  box-shadow: 0px 1px 0px #E4E8EE;
  position: relative;
}
.notification .request .user{
  display: flex;
  align-items: center;
  gap: 1em;
}
.notification .request .user .dot{
  position: absolute;
  left: -2px;top: 2px;
}
.notification .request p{
  color: #1A1F36;
  font-size: 0.87;
  font-weight: 600;
}
.notification .request p span{
  font-weight: 400;
}
.notification .request a{
  font-weight: 600;
  border-radius: 4px;
  padding: 0.5em 0.7em;
  text-decoration: none;
  display: inline-block;
  transition: all ease-in-out 200ms;
}
.notification .request a:hover{
  opacity: 0.8;
}
.notification .request a.accept{
  background: #0047B3;
  color: #ffff;
margin-right: 1em;
}
.notification .request a.reject{
  background: #FFFFFF;
  color: #091E42;
}
.exsmText{
  color: #6B788E;
  font-size: 0.7em;
  font-weight: 400;
}
/* contact table */
.commanTable.odd tbody tr:nth-child(even){
  background: #F5F6F7;
}
.commanTable.odd tbody tr:nth-child(odd){
  background: #EBEDF0;
}
.subHeading{
  color: #091E42;
  font-size: 0.9em;
  font-weight: 700;

}
.subHeading span{
  font-weight: 400;
  margin-left: 3px;
}
.sideToggle{
  display: none;
}
/* client data */
.clientData .clientHeader{
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 2em 0 !important;
}
.clientData .clientHeader p{
  color: #091E42;
  font-size: 1em;
  font-weight: 400;
}
.clientHeader a{
  color: #003E9C;
  font-weight: 600;
}
a{
  text-decoration: none;
}
.clientData{
  min-height: 100vh;
  padding: 1em;
}
.clientData .clientHeader div a:first-child{
  display: inline-block;
  margin-right: 1em;
}
.clientData .addCompany{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
}
.clientData .addCompany h1{
  font-size: 1.3em;
  color: #091E42;
  font-weight: 600;
  margin-top: 1em;
}
.clientData .addCompany h6{
  font-weight: 400;
  font-size: 1em;
  margin-bottom: 2em;
}
.clientData .mainBtn{
  padding: 0.2em 1em;
}
.clientData.profile .profileCard{
  background: #EFF8ED;
border-radius: 10px;
padding: 2em 1em;
margin-bottom: 3em;
}
.clientData.profile h1{
  font-family:"clash-g-bold";
  font-size: 1.6em;
  color: #091E42;
  margin-bottom: 1em;
}
.clientData.profile h4{
  color: #091E42;
  font-size: 1.1em;
  font-weight: 600;
}
.clientData.profile textarea,.jobTextarea{
  width: 100%;
  resize: none;
  background: #FCFCFC;
border: 1px solid #0047B3;
border-radius: 8px;
padding: 1em;
margin-bottom: 2em;
}
.jobTextarea{
  height: 7em;
  background-color: #F6F6F6;
}
.clientData.profile textarea::placeholder{
  color: #091E42;
  font-weight: 600;
  font-size: 1.1em;
  text-transform: uppercase;
  font-family: 'Archivo';
}
/* lead page */
 .inputHeader{
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-bottom: 1px solid #B0B0B0;
  padding-bottom: 0.5em;
  margin-bottom: 1.2em;
}
.inputBoxes .inputHeader .dropdown{
  display: inline-block;
}
.inputBoxes .inputHeader .win,.inputBoxes .inputHeader .fail{
  color: #091E42;
  font-size: 0.9em;
  font-weight: 400;
  background: #EFF8ED;
border-radius: 8px;
padding: 0.5em 1em;
margin: 0 7px;
}
.inputBoxes .inputHeader .win img,.inputBoxes .inputHeader .fail img{
  margin-left: 7px;
}
.inputBoxes .inputHeader .fail{
  background: #FFEAEA;
}
.mainBtn.white{
  color: #0047B3 !important;
  background: #FFFFFF;
border: 2px solid #0047B3;
border-radius: 4px;
padding: 0.3em 1em;
}
.inputBoxes .mainHeading{
  font-family:"clash-g-bold";
}

.leadSelect,.leadInput{
  background: #F6F6F6;
  border: 1px solid #0047B3;
  border-radius: 8px;
  display: block;
  width: 100%;
  padding: 0em 1em;
  height: 2.5em;
  margin-bottom: 2em;
}
.leadTextArea{
  background: #F6F6F6;
  border: 1px solid #0047B3;
  border-radius: 8px;
  display: block;
  padding: 0em 1em;
  margin-bottom: 2em;
}

.leadInputImg {
  height: 1.2em;
  position: absolute;
  top: 30px;
  left: 10px;
}

.selectOption .dropdown .btn{
  background: #F6F6F6;
  border: 1px solid #0047B3;
  border-radius: 8px;
  display: block;
  width: 100%;
  padding: 0em 1em;
  height: 2.5em;
  color: #7A8699;
  text-align: start;
}
 .dropdown .btn::after{
  display: none;
}
 .dropdown .dropdown-menu.show{
  width: 100%;
}

.leadSelect,.leadInput::placeholder{
  color: #7A8699;
  font-size: 1.1em;
  font-weight: 400;
}
.dropdown .dropdown-item{
  color: #091E42;
}
.dropdown .dropdown-item:hover{
  color: #003E9C;
}
.dropdown .btn .dropDownArrow{
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.dropdown .btn.show .dropDownArrow img{
  transform:rotate(180deg)
}
.leadSelect option{
  color: black;
}
.leadSelect{
  -webkit-appearance: none;
  -moz-appearance: none;
  background-image: url("./assets/sdown.svg");
  background-repeat: no-repeat;
  background-position-x: 95%;
  background-position-y: 50%;
}
.uploadBox{
  background: #FFFFFF;
border-radius: 16px;
padding: 1.5em;
}
.uploadBox .upload{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  border: 1px dashed #D0D5DD;
border-radius: 4px;
padding: 1em;
cursor: pointer;
}
.mainText{
  color: #091E42;
font-weight: 400;
font-size: 1em;
}
#uploadFile{
  display: none;
}
.boxSection{
  margin-bottom: 2em;
}
.inputBoxes label{
  margin-bottom: 0.7em;
}
/* lead list table */
.commanTable table td .dropdown{
  display: inline-block;
}
.commanTable .resp{
  padding: 0.8em 0.8em;
  font-size: 11px;
  font-weight: 400;
  color: #091E42;
  margin: 0 5px;
  border-radius: 8px;
}
.commanTable .resp img{
  margin-left: 5px;
}
.commanTable .resp.silent{
