}
.commanTable .resp.gray{
  background: #DFE2E6;
}
.commanTable .resp.lost{
  background: #FEAAAA;
}
.commanTable .resp.win{
  background: #BDE4B5;
}
.clientData table thead th:first-child{
  border-top-left-radius: 4px;
  border-bottom-left-radius: 4px;
}
.clientData table thead th:last-child{
  border-top-right-radius: 4px;
  border-bottom-right-radius: 4px;
}
.paginationBox{
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 2em;
}
.pagination .page-item{
  margin: 0 0.3em;
}
.pagination .page-item .page-link{
  color: #091E42;
  font-weight: 700;
  border-radius: 4px;
}
.pagination .page-item.active .page-link{
  background: #FFFFFF;
  border: 1px solid #0047B3;
  border-radius: 4px;
  color: #0047B3;
}
/* hr table */
.commanTable.hrTable table td:first-child img,
.commanTable.candidate table td:first-child img,
.commanTable.payrollTable table td:first-child img
{
margin-right: 10px;
}
.commanTable.candidate tr td:last-child img,
.commanTable.payrollTable tr td:last-child img
{
  margin-left: 1em;
}

.clientData.hrDashboard .sortSelect .selectOption{
  display: flex;
  align-items: center;
  justify-content: end;
 margin-top: 2em;
 margin-bottom: 2em;
}
.clientData.hrDashboard .sortSelect label{
  display: inline-block;
  /* margin-top: -2em; */
  margin-right: 1em;
}
.clientData.hrDashboard .sortSelect select{
  width: 15em;
}
.clientData.profile .userTitle{
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.clientData.profile .smText{
  text-transform: uppercase;
}
/* hr employ  */
.mainBtn.red{
  border: 2px solid #B12222;
  color: #B12222 !important;
  background-color: transparent;
}
/* attendence */
.commanTable.attendence tr td:last-child img,
.commanTable.acc tr td:last-child img ,
.commanTable.subs tr td:last-child img
{
  margin: 0 7px;
}
.commanTable.attendence tr td:last-child img:hover,.commanTable.acc tr td:last-child img:hover {
  opacity: 0.7;
}
.attendDate {
  display: flex;
  align-items: center;
  gap: 2em;
  padding-left: 5em;
  margin: 1em 0;
}
.attendDate .dateInp{
  position: relative;
}
.attendDate .dateInp img{
  position: absolute;
  right: 5px;
  top: 50%;
  transform: translateY(-50%);
}
.attendDate input{
  width: 100%;
  background: #F6F6F6;
border: 1px solid #0047B3;
border-radius: 8px;
padding: 0.5em 2em;
padding-right: 0em;
color: #7A8699;
}
input[type="date"]::-webkit-inner-spin-button,
input[type="date"]::-webkit-calendar-picker-indicator {
    /* display: none; */
    /* -webkit-appearance: none; */
    opacity: 0;
    width: 2em;
    z-index: 999;
}
.emptyTable {
   text-align: center;
   background-color: white;
   padding: 1em 0;
}

.MakePayment{
  background: #E6F0FF;
border-radius: 8px;
padding: 0.5em;
color: #091E42;
}
.MakePayment.silent{
  opacity: 0.5;
}
.payroll{
  position: relative;
}
.payroll .nav-tabs{
  position: absolute;
  top: 12em;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: white;
  width: 97%;
}
.payrollTable table{
  margin-top: 4em;
}
.payrollTable.payslip table{
  margin-top: 3.5em;
}
.clientData.payroll .nav-tabs .nav-link{
  background: #FFFFFF;
  border-radius: 0;
  /* border: 0.5px solid #C4C4C4; */
  color: #7A8699;
  font-weight: 400;
}
.clientData.payroll .nav-tabs .nav-link.active{
  background: #E6F0FF;
  color: #003E9C;
border-top: 2px solid #003E9C;
}
.commanTable.payrollTable table thead th,.commanTable.acc table thead th{
  border-radius: 0;
}

.commanTable.acc .totalDetail{
  text-align: center;
  background: #E6F0FF;
border: 1px solid #2B7FFF;
padding: 1em 0;
}
.commanTable.acc .totalDetail.red{
  background: #FFEAEA;
border: 1px solid #FD3030;
}

.substitudeCard{
  text-align: center;
  background: #EFF8ED;
border: 1px solid #5FBC4A;
border-radius: 16px;
padding: 1em 0;
margin-bottom: 1em;
}
.substitudeCard.blur{
  background: #E6F0FF;
  border: 1px solid #2B7FFF;
}
.substitudeCard.blur h1{
  filter: blur(7.6px);
  font-weight: 700;
}
.substitudeCard.red{
  background: #FFEAEA;
border: 1px solid #FD3030;
}
.substitudeCard .mainText{
  margin-bottom: 1em;
}
.substitudeCard h1{
  color: #091E42;
  font-weight: 400;
  font-size: 2.5em;
}
/* offcanvas */
.offcanvas.show:not(.hiding), .offcanvas.showing {
  width: 40em;

  background: #F6F6F6;
}
.offcanvas-header{
  background: #7AC769;
  color: #091E42;
}
.offcanvas-title{
  font-weight: 600;
  font-size: 1.5em;
}
.popUpTextArea{
background: #FCFCFC;
border: 1px solid #0047B3;
border-radius: 8px;
width: 100%;
padding: 1em;
height: 7em;
