/* media queryy */
@media(max-width:992px){
  .payroll .nav-tabs{
    top: 12em;
  }
  .payrollTable table{
    margin-top: 7em;
  }
  .payrollTable.payslip table{
    margin-top: 3.5em;
  }
  .rightSideBar{
    margin: 0;
  }
  .clientHeader.substitute{
    flex-direction: column;
    align-items: flex-start;
  }
  .attendDate.monthly, .attendDate.summary{
    flex-direction: column;
    align-items: flex-start;
  }
  .navbar{
    z-index: 999999999;
   
  }
  .attendDate {
  display: flex;
  align-items: center;
  gap: 1em;
  padding-left: 0em;
}
  .clientData.profile .smText{
    margin-top: 2em;
  }
  .inputBoxes .inputHeader{
    flex-direction: column;
    justify-content: start;
    align-items: start;
  }
  .mainBtn.white{
    font-size: 0.6em;
  }
  .inputBoxes .inputHeader .win, .inputBoxes .inputHeader .fail{
    font-size: 0.7em;
  }
  .inputBoxes .inputHeader .win img,.inputBoxes .inputHeader .fail img{
    margin-left: 2px;
  }
  .progressCard{
    margin-bottom: 1em;
  }
  .loginPage form{
    padding: 5em 1em;
  }
  .sideBar{
    position: absolute;
    top: 4.2em;
    left: -500px;
    z-index: 99999;
    transition: all ease-in-out 400ms;
  }
  .mainContent{
    width: 100%;
  }
  .commanTable{
    overflow-x: auto;
  }
  .commanTable table{
    width: 1000px;
  }
  .navbar{
    position: relative;
    padding: 2em 0;
  }
  .navbar .navbar-toggler{
    position: absolute;
    right: 1em;
    top:12px;
  }
  .navBarSection .navBar{
    flex-direction: column;
    align-items: start;
  }
  .navBarSection .navBar .searchBox {
    margin: 2em 0;
  }
  .navBarSection .navBar .searchBox input{
    width: 70%;
  }
  .sideToggle{
    display: inline-block;
    width: 2em;
    top: 12px;
    left: 1em;
    position: absolute;
  }
  .show{
    left: 0;
  }
}

